import React from 'react'
import Particles from 'react-particles-js';

const Footer = () => {
  return (
    <div className='footer'>
      <Particles 
              params={{
                "particles": {
                    "number": {
                        "value": 19,
                        "density": {
                            "enable": true,
                            "value_area": 500
                        }
                    },
                    "line_linked": {
                        "enable": false
                    },
                    "move": {
                        "speed": 7,
                        "out_mode": "out"
                    },
                    "shape": {
                        "type": [
                            "images",
                        ],
                        "images": [
                            {
                                "src": "static/img/icons/github.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-php.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-css.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-js.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-html.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-react.svg",
                                "height": 20,
                                "width": 23
                            },
                        ]
                    },
                    "color": {
                        "value": "#CCC"
                    },
                    "size": {
                        "value": 30,
                        "random": false,
                        "anim": {
                            "enable": true,
                            "speed": 4,
                            "size_min": 12,
                            "sync": false
                        }
                    }
                },
                "retina_detect": false
            	}}
              style={{
                width: '100%',
                wheight: '100%',
                position:'absolute',
                top:0,
                left:0
              }}
            />
        <div className="content-wrapper">
           <div className="sitemap">
             <div className="item responsive">
                <img className='logo' src='static/img/logo.png'/>
             </div>
             <div className="item">
                <div className="site-anchors">
                    <i><a href="#about">About</a></i>
                    <i><a href="#works">Works</a></i>
                    <i><a target='_blank' href="https://github.com/Gherciu">Follow on GitHub</a></i>
                </div>
             </div>
           </div>
           <div className="copyright">
                ¬© 2019 Gherciu Gheorghe. All Rights Reserved
           </div>
        </div>
        <style jsx>{`
            .footer{
                background: linear-gradient(45deg,#6d4dd9,#14badc);;
                min-height:130px;
                width:95%;
                margin: 0 auto;
                border-radius:200px 200px 0px 0px;
                color:#fff;
                border-top:12px solid #E0E5F6;
                position:relative;
                overflow: hidden;
            }
            .content-wrapper{
                padding:60px 10px 20px!important;
            }
            .sitemap{
                display: flex;
                justify-content: space-between;
            }
            .sitemap .item{
                flex:1;
                min-height:60px;
                margin-bottom: 10px;
                display: flex;
                flex-direction:column;
                align-items: flex-start;
            }
            .sitemap .item:last-of-type{
                align-items: flex-end;
            }
            .sitemap .item i a{
                text-decoration:none;
                color:#fff;
            }
            .site-anchors{
                display: flex;
                flex-direction:column;
                align-items: flex-start;
            }
            @media (max-width:1250px){
                .sitemap .item{
                    align-items: center;
                }
                .sitemap .item:last-of-type{
                    align-items: center;
                }
                .copyright{
                    margin-left:80px;
                }
                
            }
            @media (max-width:840px){
                    .footer{
                        border-radius: 0px;
                        margin:0px;
                        width: 100vw;
                    }
            }
            @media (max-width:500px){
                .sitemap{
                    display: flex;
                    flex-direction: column;
                }
                .site-anchors{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
                .copyright{
                    margin: 10px;
                    text-align: center;
                }
            }
        `}</style>
    </div>
  )
}

export default Footer
